.container{color:#fff;min-height:100vh;padding-top:64px;padding-inline:1rem;display:flex;flex-direction:column;gap:3rem;overflow-x:hidden}.header{width:100%}.videoWrapper{position:absolute;top:0;left:0;width:100%;height:400px;overflow:hidden;z-index:-1}.videoWrapper::after{content:"";position:absolute;bottom:0;left:0;width:100%;height:250px;background:linear-gradient(to bottom, transparent, black);pointer-events:none;z-index:2}.videoBackground{width:100%;height:100%;object-fit:cover;pointer-events:none;opacity:.4}.aboutSection{text-align:center;max-width:800px;margin:0 auto;display:flex;flex-direction:column;gap:1rem;margin-bottom:3rem;margin-top:4rem}.aboutSection h2{font-size:2rem;font-weight:600}.aboutSection p{font-size:1.1rem;color:#ccc}.aboutMeBlock{display:flex;max-width:1000px;margin:0 auto;padding-top:3rem;padding-bottom:4rem;align-items:flex-start;gap:4rem}@media(max-width: 768px){.aboutMeBlock{flex-direction:column;gap:2rem}}.aboutMeBlock .left{width:fit-content;min-width:120px;max-width:160px;flex:0 0 auto}.aboutMeBlock .left h3{font-size:1.4rem;font-weight:600;color:#fff;margin:0}.aboutMeBlock .right{flex:1;display:flex;flex-direction:column;gap:1.2rem;color:#ccc}.aboutMeBlock .right p{font-size:1.05rem;line-height:1.7}.aboutMeBlock .right ul{list-style-type:disc;padding-left:1.5rem;display:flex;flex-direction:column;gap:.4rem}.aboutMeBlock .right ul li{line-height:1.6}.processSection{display:flex;flex-direction:column;gap:6rem;max-width:1000px;margin:0 auto;padding-bottom:6rem}.step{position:relative;min-height:220px;display:flex;flex-direction:column;gap:1rem}.leftRight{display:flex;justify-content:space-between;gap:2rem;opacity:0;transform:translateY(40px);will-change:transform,opacity}@media(max-width: 768px){.leftRight{flex-direction:column}}.left{flex:1}.left .stepNumber{font-size:1.2rem;font-weight:bold;color:#888}.left .stepTitle{font-size:1.5rem;margin-top:.3rem}.left .stepDescription{margin-top:.8rem;color:#bbb;line-height:1.6;font-size:1.05rem}.right{flex:1}.right .toolGrid{display:flex;flex-wrap:wrap;gap:.5rem;margin-top:.5rem}.right .toolGrid span{background:#fff;color:#000;padding:.4rem .8rem;font-size:.85rem;font-weight:500;border-radius:999px}.middle{display:flex;flex-direction:column;align-items:center;position:absolute;top:0;left:50%;transform:translateX(-50%);height:100%;pointer-events:none}.middle .dot{width:14px;height:14px;border-radius:50%;background-color:rgba(0,0,0,0);border:2px solid hsla(0,0%,100%,.4);transition:background-color .3s ease,border-color .3s ease}.middle .dot.active{background-color:#fff;border-color:#fff}.middle .line{flex-grow:1;width:2px;background-color:hsla(0,0%,100%,.2);margin-top:.5rem}@media(max-width: 768px){.middle{display:none}}.deliverySection{max-width:1000px;margin:0 auto;padding-bottom:6rem;display:flex;flex-direction:column;align-items:center;text-align:center;gap:2rem;padding-inline:1rem}.deliveryTitle{font-size:2rem;font-weight:600;color:#fff}.deliveryIntro{font-size:1.1rem;color:#ccc;max-width:720px;line-height:1.6}.deliveryGrid{display:grid;grid-template-columns:repeat(auto-fit, minmax(260px, 1fr));gap:2rem;width:100%;margin-top:2rem}.deliveryCard{background-color:#111;border:1px solid hsla(0,0%,100%,.1);border-radius:12px;padding:1.5rem;text-align:left;transition:transform .3s ease,box-shadow .3s ease}.deliveryCard:hover{transform:translateY(-4px);box-shadow:0 8px 20px rgba(0,0,0,.3)}.deliveryCard h4{font-size:1.2rem;font-weight:600;margin-bottom:.6rem;color:#fff}.deliveryCard p{font-size:1rem;color:#aaa;line-height:1.5}